import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { appUrl } from 'src/app/app-url.module';

@Component({
  selector: 'app-manage',
  templateUrl: './manage-product.page.html',
  styleUrls: ['./manage-product.page.scss'],
})
export class ManagePage implements OnInit {
  manageNavigate: any[] = [
    {
      label: 'ສິນຄ້າ',
      icon: 'id-card',
      link: appUrl.product,
    },
    {
      label: 'ສັ່ງຊື້ສິນຄ້າ',
      icon: 'cube',
      link: '',
    },
    {
      label: 'ປະເພດສິນຄ້າ',
      icon: 'folder-open',
      link: appUrl.category,
    },
    {
      label: 'ຫົວໜ່ວຍ',
      icon: 'server',
      link: appUrl.unit,
    },
    {
      label: 'ຕຳແໜ່ງ',
      icon: 'locate',
      link: appUrl.zone,
    },
  ];
  url = appUrl;

  constructor(private router: Router) {}

  ngOnInit() {}

  isNavigate(link: string) {
    if (this.url.home === link) {
      return;
    }
    this.router.navigate([this.url.home, link]);
  }
}
